import { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom'

import Home from './components/Home/Home'
import SuccessCheckout from './components/SuccessCheckout'
import ErrorCheckout from './components/ErrorCheckout'

function App() {
  return (
    <Router>
      <div className='App'>
        <ul>
          <li><Link to='/monetized-api'>Home</Link></li>
          <li><Link to='/monetized-api/success'>Success</Link></li>
          <li><Link to='/monetized-api/error'>Error</Link></li>
        </ul>
      </div>
      <Switch>
        <Route path='/monetized-api' component={Home} />
        <Route path='/monetized-api/success' component={SuccessCheckout} />
        <Route path='/monetized-api/error' component={ErrorCheckout} />
      </Switch>
    </Router>
  );
}

export default App;
